:root {
    --main-background: #1e1e1e;
    --nav-pane-background: #252526;
    --sidebar-background: #333333;
    --font-color: #fff;
    --link-font-color: #fff;

    --hover-glow-background: #3f3f3f;

    --button-background: #078263;
    --button-text-color: #fff;

    --list-border-color: #444;

    --note-font-color: #eee;

    --active-tree-editor-background: #292929;

    --active-context-background: #3f3f3f;

    --default-tag-background: #777;
    --context-tag-background: darkslategray;

    --task-context-background: #00503C;
    --task-project-background: #666;

    --settings-info-background: #222;
}

.light-theme {
    --main-background: #fff;
    --nav-pane-background: #ccc;
    --sidebar-background: #ddd;
    --font-color: #000;
    --link-font-color: #000;

    --hover-glow-background: orange;

    --button-background: #555;
    --button-text-color: #fff;

    --list-border-color: #aaa;

    --note-font-color: #222;

    --active-tree-editor-background: #ddd;

    --active-context-background: #bbb;

    --default-tag-background: #ccc;
    --context-tag-background: darkorange;

    --task-context-background: lightblue;
    --task-project-background: lightgreen;

    --settings-info-background: #aaa;
}