$green-100: #39A389;
$green-200: #1B9A7A;
$green-300: #078263;
$green-400: #006B50;
$green-500: #00503C;

$mobile-width: 768px;

* {
    box-sizing: border-box;
}

html, body, #root, #root>section {
    height: 100%;
    background: var(--main-background);
    color: var(--font-color);
    padding: 0;
    margin: 0;

    font-family: Arial, Helvetica, sans-serif;
    font-size: 16px;
}

.navbar {
    background: var(--nav-pane-background);
    position: fixed;
    z-index: 10;
    width: 100%;
    display: flex;
    justify-content: space-between;

    div {
        display: flex;
        height: 52px;
    }
}

a {
    color: var(--link-font-color);
    text-decoration: none;
}

.sidebar {
    transition: width .1s;
    width: 200px;
    background-color: var(--sidebar-background);

    ul {
        padding: 0 1rem;
        list-style-type: none;

        li {
            border-bottom: 1px solid #ccc;
            padding: 0.5rem;
        }
    }
    
    @media screen and (max-width: $mobile-width) {
        width: 100%;
        position: fixed;
        top: 52px;
        z-index: 10;
    }
}

.list {

    & > * {
        border-top: 1px solid var(--list-border-color);
        border-width: 1px 0;
    }

    & > *:last-child {
        border-bottom: 1px solid var(--list-border-color);
    }

    .header {
        display: flex;
        justify-content: space-between;
        background: #666;
        padding: 0.6rem;
        align-items: center;

        &.link {
            cursor: pointer;
        }
    }

    .item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0.5rem;
    }
}

.note {
    flex-wrap: wrap;

    .note-details {
        display: flex;
    }

    .note-date {
        display: flex;
        align-items: center;
        margin-left: auto;
        font-size: 0.9rem;
        color: #bbb;
    }

    @media screen and (max-width: $mobile-width) {

        .note-title {
            width: 100%;
        }

        .note-details {
            flex-grow: 1;
            align-items: space-between;
            justify-content: space-between;
            flex-direction: column;
        }
    }
}

.task {
    display: grid !important;

    grid-template-columns: min-content 1fr max-content;

    grid-template-areas: 
        "box title details";

    .task-checkbox {
        grid-area: box;
        display: flex;
        justify-content: center;
        align-items: center;
        padding-right: 8px;
    }

    .task-title {
        grid-area: title;
    }

    .task-details {
        grid-area: details;
    }

    @media screen and (max-width: $mobile-width) {
        grid-template-areas: 
        "box title"
        "box details";
    }
}

.tags {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;

    & > * {
        margin-right: 0.2rem;
    }
}

.tag {
    padding: 0.15rem 0.3rem;
    background: var(--default-tag-background);
    white-space: nowrap;
    border-radius: 3px;
    color: var(--font-color);

    &.is-context {
        background: var(--context-tag-background);
    }
}

.button {
    background: var(--button-background);
    font-size: 1rem;
    color: var(--button-text-color);
    text-decoration: none;
    padding: .375rem .75rem;
    border-radius: .1rem;
    border: 0;
    line-height: 1.5;
    display: inline-flex;
    justify-items:  center;
    align-items: center;
    cursor: pointer;

    &.small {
        padding: .2rem .4rem;
        line-height: 1;
    }

    &.inactive {
        background: #444;
    }
}

.main {
    width: 100%;
    overflow-y: auto;
    background: var(--main-background);
}

.input {
    &.is-large {
        padding: .3rem;
        font-size: 1.2rem;
    }


    &.is-wide {
        width: 100%;
    }
}

input[type=date].input {
    &.is-large {
        padding: .05rem;
    }
}

.flex {
    display: flex;
}

.hover-glow:hover {
    background: var(--hover-glow-background);
}